"use client";
import { useEffect, useState } from "react";
import { useParams } from "next/navigation";

export default function ReportViewPage() {
  const { token } = useParams();
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const [report, setReport] = useState<any>(null);
  const [error, setError] = useState("");

  useEffect(() => {
    fetch(`http://192.168.100.66:4000/api/report/${token}`)
      .then((res) => res.ok ? res.json() : Promise.reject("Invalid or expired link"))
      .then(setReport)
      .catch(() => setError("This link is invalid or has already been used."));
  }, [token]);

  if (error) return <div className="text-red-500">{error}</div>;
  if (!report) return <div>Loading...</div>;

  const student = report.student || {};
  const score = report.score || {};
  const subjects = Object.entries(score)
    .filter(([key]) =>
      !["rank", "grade", "total", "average", "absent"].includes(key)
    );

  return (
    <div className="max-w-xs mx-auto bg-white rounded-2xl shadow-lg p-4 mt-4 border-2 border-blue-100 font-[Kantumruy]">
      {/* Logo */}
      <div className="flex items-center gap-2 mb-2">
        <img src="https://ik.imagekit.io/edgrade/logo.png" alt="EdGrade" className="w-8 h-8" />
        <span className="text-blue-700 font-bold text-2xl">EdGrade</span>
      </div>
      <hr className="my-2 border-dotted border-gray-300" />

      {/* Avatar and Info */}
      <div className="flex items-center gap-4 mb-4">
        <div className="w-24 h-24 rounded-full bg-orange-200" />
        <div>
          <div className="font-bold">ឈ្មោះ៖</div>
          <div>{student.name || "-"}</div>
          <div>{student.gender || ""}</div>
          <div className="text-xs">{student.address || ""}</div>
        </div>
      </div>

      {/* Table */}
      <div className="bg-gray-600 text-white px-3 py-2 rounded-t font-bold">មេរៀន</div>
      <table className="w-full border-collapse">
        <tbody>
          {subjects.map(([subject, value], idx) => (
            <tr key={subject} className={idx % 2 === 0 ? "bg-gray-100" : ""}>
              <td className="px-2 py-1 text-[15px]">{subject}</td>
              <td className="px-2 py-1 text-right">{String(value)}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Summary */}
      <div className="flex justify-between mt-4 text-[15px]">
        <div>
          <div>និទ្ទេស: <span className="font-bold">{score.grade || "-"}</span></div>
          <div>អវត្តមាន: <span className="font-bold">{score.absent || "0"}</span></div>
        </div>
        <div>
          <div>ចំនួនតម្លៃ: <span className="font-bold">{score.total || "-"}</span></div>
          <div>សិស្សសរុប: <span className="font-bold">{score.rank || "-"}</span></div>
          <div>មធ្យមសិក្សា: <span className="font-bold">{score.average || "-"}</span></div>
          <div>Rank: <span className="font-bold">{score.rank || "-"}</span></div>
        </div>
      </div>
    </div>
  );
}